.about_container {
    display: grid;
    grid-template-columns: 1fr;
    gap: 15%;
}

.about_cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
}

.about_card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition)
}

.about_card:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about_icon {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about_card h5 {
    font-size: 0.95rem;
}

.about_card small {
    font-size: 0.7rem;
    color: var(--color-b-light);
}

.about_content  {
    margin: 2rem 0 2.6rem;
    color: var(--color-b-light);
    text-align: center;
}

.about_content p {
    margin: 2rem 0 2rem;
}

/* ======== MEDIA QUERIES (MEDIUM DEVICES) ======= */
@media screen and (max-width: 1024px) {
    .about_container {
        grid-template-columns: 1fr;
        gap: 0;
    }

    .about_me {
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about_content p {
        margin: 1rem 0 1.5rem;
    }

    .about_cards {
        grid-template-columns: 1fr 1fr;
        /* width: 100vw; */
        display: flex;
        justify-content: center;
    }

    /* .about_card {
        width: 33%;
    }  */

    
}

/* ======== MEDIA QUERIES (SMALL DEVICES) ======= */
@media screen and (max-width: 600px) {
    .about_me {
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about_cards {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
        /* width: 33%; */
    }

    /* .about_card {
        width: 33%;
    } */

    .about_content {
        text-align: center;
    }

    .about_content p {
        margin: 1.5rem 0;
    }
}